.download {
  background: $download-bg;

  &-content {
    @include center;
    flex-direction: column;
    flex-basis: $center-width;

    .title {
      @include center;
      flex-basis: 100px;
      font-size: 64px;
      font-weight: 600;
      @include title-text-shadow($download-bg);
    }
  }
}